/*
 *  FAQ Accordion
 *    Styles for the faq accordion
 *
 *  Version:
 *    1.0.0 - 2021/09/21
 */

.c-faq-accordion {
  margin: 0 auto;

  &__slide {
    display: none;
    margin: 0 auto;
    text-align: left;
    padding: 2rem 0 1.9375rem;
    max-width: 43.1875rem;
    border-bottom: 0.0625rem solid $s-color--border-subdued-20;

    &:first-child {
      border-top: 0.0625rem solid $s-color--border-subdued-20;
    }

    &.is-visible {
      display: block;
    }

    &.is-visible-body>.c-faq-accordion__body {
      display: block;
    }
  }

  &__title {
    margin-bottom: 0;
  }

  &__body {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    display: none;

    & p {
      @include t-font--paragraph-2;
      line-height: 1.5rem;
    }
  }

  &__toggle {
    margin: 0;

    &--hide,
    &--show {
      margin-top: 0.5rem;
      text-transform: capitalize;
      color: $s-color--font-focus;
      cursor: pointer;
      display: none;
    }

    .is-visible {
      display: block;
    }
  }

  &__questions-card {
    width: 100%;
    max-width: 43.1875rem;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -3.125rem);
    opacity: 0;
    transition: 0.8s cubic-bezier(0.25, 1, 0.5, 1);

    &--show {
      opacity: 1;
      transform: translate(-50%, 0);
      z-index: 1;
    }

    &--fade {
      opacity: 0;
      transform: translate(-50%, 3.125rem);
      transition: 0.8s cubic-bezier(0.25, 1, 0.5, 1);

    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    margin: 0;

    &__slide {
      border-bottom: none;
      max-width: none;
      padding-top: 0;
      padding: 0 0 2.5rem;


      &:first-child {
        padding-top: 0;
        border-top: none;
      }
    }

    &__title {
      margin-bottom: 0;
    }

    &__body {
      margin-top: 1.5rem;
      margin-bottom: 1.5rem;
    }

    &__toggle {
      &--hide,
      &--show {
        margin-top: 0.5rem;
      }
    }
  }
}