/*
 *  FAQ category
 *    Styles for the FAQ header
 *
 *  Version:
 *    1.0.0 - 2021/09/21
 */

.c-faq {
  &__inner {
    padding: 3.6875rem 1.25rem 6.875rem 1.25rem;
    background-color: $s-color--background-subdued;
  }

  &__title {
    margin-bottom: 1rem;
    text-align: center;
  }

  &__subtitle {
    margin-bottom: 2.5rem;
    text-align: center;
  }

  &__description {
    color: $s-color--font-focus;
    margin-bottom: 1.5rem;
    text-align: center;
  }

  &__categories {
    padding: 0 1.25rem;
    margin-bottom: 2.5rem;

    &-nav {
      position: relative;
      max-width: 25rem;
      margin: 0 auto;
      border: 0.0625rem solid $s-color--border-focus;
      border-radius: 0.25rem;
      color: $s-color--font-subdued;
      background-color: $s-color--background;

      &.is-active {
        border-bottom: 0;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
      }
    }

    &-default {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 1rem 1.1875rem 1rem 1.25rem;
    }

    &-chevron {
      @include t-anim--transition(transform, $ease: linear);
      height: 0.875rem;
      width: 0.875rem;

      .is-active & {
        transform: rotate(-90deg);
      }
    }

    &-body {
      position: absolute;
      top: 100%;
      left: -0.0625rem;
      width: calc(100% + 0.125rem);
      border: 0.0625rem solid $s-color--border-focus;
      border-radius: 0.25rem;
      border-top: 0;
      border-top-right-radius: 0;
      border-top-left-radius: 0;
      background-color: $s-color--background;
      overflow: hidden;
      z-index: $s-z--text-overlay;
      display: none;
    }

    &-button {
      padding: 1rem 0.625rem 1rem 1.25rem;
      display: flex;
      align-items: center;
      width: 100%;
      justify-content: space-between;
      cursor: pointer;

      &.is-active {
        display: none;
      }

      &-name {
        font-size: 0.9375rem;
        margin: 0;
        text-transform: capitalize;
      }

      &-icon {
        height: 1.5rem;
        width: 1.5rem;
        margin-right: 1.3125rem;
      }
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    &__inner {
      padding: 3.8625rem #{'min(10%, 12.5rem)'} 7.0875rem;
    }

    &__inner-content {
      display: grid;
      grid-template-columns: 27.86% 60.75%;
      gap: 11.39%;
      max-width: 62.5rem;
      margin: 0 auto;
    }

    &__title {
      margin-bottom: 0.6875rem;
    }

    &__subtitle {
      @include t-font--heading-2-laptop;
      margin-bottom: 5.175rem;
    }

    &__categories {
      padding: 0.75rem 0 0 0;

      &-nav {
        max-width: none;
        border: 0;
        border-radius: 0;
        background-color: transparent;
      }

      &-default {
        display: none;
      }

      &-body {
        // To override js inline styles from mobile if necessary
        display: block!important;
        position: initial;
        top: initial;
        left: initial;
        width: auto;
        border: 0;
        border-radius: 0;
        overflow: initial;
        background-color: transparent;
        z-index: initial;
      }

      &-button {
        @include t-font--heading-5-laptop;
        position: relative;
        display: block;
        width: auto;
        padding: 0;
        margin-bottom: 1.2188rem;

        &::after {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 0.0938rem;
          background-color: transparent;
          content: "";
        }

        &:last-child {
          margin-bottom: 0;
        }

        &.is-active {
          display: block;
          color: $s-color--font-focus;

          &::after {
            background-color: $s-color--background-impact;
          }
        }
      }
    }

    &__description {
      margin-bottom: 1.5rem;
      text-align: left;
      color: $s-color--font;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      padding-top: 5.875rem;
      padding-bottom: 11.8125rem;
    }

    &__inner-content {
      max-width: 72.1875rem;
    }

    &__title {
      @include t-font--heading-5-laptop;
    }

    &__subtitle {
      @include t-font--heading-2-desktop;
      margin-bottom: 8.625rem
    }

    &__categories {
      &-button {
        @include t-font--heading-5-desktop;
      }
    }
  }
}